import { defineStore, storeToRefs } from "pinia";
import { ref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from "@/store/admin.store";
import { utils, write } from 'xlsx';
import { s2ab } from '@/modules/utility.module';

export const useCertificateStore = defineStore("certificate", ()=>{



	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert } = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);

	const certTestId = ref(null);

	const certificates = ref([]);

	const getCertificates = ()=>{
		toggleProcessLoader();

		const req = new XMLHttpRequest();
		req.addEventListener("load", ()=>{

			const res = req.response;
			// alert(res);
			const json = JSON.parse(res);

			if(json.status == true){
				const { body } = json;
				certificates.value = body;
			}else {
				alert(json.message);
			}
			
			toggleProcessLoader();
		});
		req.open("GET", `${backend.value}/api/certificate/${certTestId.value}`);
		req.setRequestHeader("Authorization", `bearer ${token.value}`);
		req.send();
	}

	const editCertificate = (id)=>{
		const certificateToEdit = certificates.value.filter((certificate)=> certificate.id == id)[0];

		toggleProcessLoader();

		const req = new XMLHttpRequest()
		req.addEventListener("load", ()=>{

			const res = req.response;
			
			const json = JSON.parse(res);

			if(json.status == true){
				toggleProcessLoader();
			}else {
				alert(json.message);
				console.log(json);
			}
			
		});
		req.open("PUT", `${backend.value}/api/certificate/`);
		req.setRequestHeader("Authorization", `bearer ${token.value}`);
		req.send(JSON.stringify(certificateToEdit));
	}

	const deleteCertificate = (id)=>{

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		req.addEventListener("load", ()=>{

			const res = req.response;
			
			const json = JSON.parse(res);

			if(json.status == true){
				const arr = certificates.value.filter((certificate)=> certificate.id != id);
				certificates.value = arr;
				toggleProcessLoader();
			}else {
				alert(json.message);
			}
			
		});
		req.open("DELETE", `${backend.value}/api/certificate/${id}`);
		req.setRequestHeader("Authorization", `bearer ${token.value}`);
		req.send();
	}

	const generateList = (id)=>{
		toggleProcessLoader();
			const req = new XMLHttpRequest();
			req.addEventListener("load", ()=>{
				const res = req.response;
				try {
					const json = JSON.parse(res);
					const {body} = json;
					
					// Now create the result json
					const resultsForTest = body;

					const arr = [];

					// Get the candidate personas
					const req = new XMLHttpRequest();
		
					req.addEventListener("load", ()=>{
						
						const res = req.response;
						try {
							const json = JSON.parse(res);
						
							if(json.status == true){
								const personas = json.data;
								for(var a=0; a<resultsForTest.length; a+= personas.length){
									const item = {
										'S/N': a+1,
										'TEST NAME': resultsForTest[a].test_name,
										'SCORE': resultsForTest[a].score,
									}


									for(var b=0; b<personas.length; b++){
										try {
											item[resultsForTest[a + b].persona_data.toUpperCase()] = resultsForTest[a + b].persona_value;
										}catch(e){
											e;
										}
										
									}

									item['Certificate Link'] = `${backend.value}/certificate/?cerid=${id}&cid=${resultsForTest[a].candidate_id}`

									arr.push(item);
								}

								const sheet = utils.json_to_sheet(arr);
								const workbook = utils.book_new();
								utils.book_append_sheet(workbook, sheet, 'Result');
								// Convert the workbook to a binary string
								const wbBinary = write(workbook, { bookType: 'xlsx', type: 'binary' });
								// Convert the binary string to a Blob
								const blob = new Blob([s2ab(wbBinary)], { type: 'application/octet-stream' });

								// Create an anchor tag and set its attributes
								const anchor = document.createElement('a');
								anchor.href = URL.createObjectURL(blob);
								anchor.download = 'result.xlsx';

								// Append the anchor tag to the document body and click it
								document.body.appendChild(anchor);
								anchor.click();
								document.body.removeChild(anchor);
							

								toggleProcessLoader();
							}else {
								appAlert("An error just occured, couldn't get the personas, refreshing");

							}
						}catch(e){
							appAlert("An error just occured, it seems to be internal, please contact the admin");
						}
					})

					req.addEventListener("error", (e)=>{
						appAlert("An error just occured while trying to get the personas, please reload the page")
						console.log(e);
					})

					req.open("GET", `${backend.value}/api/persona_data/${certTestId.value}`);
					req.setRequestHeader("Content-Type","application/json");
					req.setRequestHeader("Authorization", `bearer ${token.value}`)
					req.send();

					
				}catch(e){
					console.log(e);
				}
			});
			req.open("GET", `${backend.value}/api/result/${certTestId.value}`);
			req.setRequestHeader("Content-Type", "application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`);
			req.send();
	}

	return {
		certTestId,
		certificates,
		getCertificates,
		editCertificate,
		deleteCertificate,
		generateList
	}
})

