
<script setup>
import { 
	onMounted, 
	ref, 
	// unref, 
	// computed 
} from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';
import { useCertificateStore } from '@/store/certificate.store';


const certificateStore = useCertificateStore();
const { certificates } = storeToRefs(certificateStore);
const { getCertificates, editCertificate, deleteCertificate, generateList } = certificateStore;

const searchStr = ref("");

onMounted(()=>{
	getCertificates();
})

// Add test data
const addCertificatePayload = ref({
	name: null,
	cutoff: null,
	html: ""
})

// const addAnswerBtnClick = ()=>{
// 	createAnswer(addAnswerPayload);
// }
</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<h5 class="text-center p-3">
			Showing certificates
		</h5>
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addCertificateModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getAnswers">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!certificates.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No certificates available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col text-bold text-light p-2">
					Certificate name
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Cutoff
				</div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="certificate in certificates"
				:key="certificate.id"
				:id="certificate.id"
			>
				<div class="col">
					{{ certificate.certificate_name }}
				</div>
				<div class="col">
					{{ certificate.cutoff_mark }}
				</div>
				<div class="col">
					<a target="_blank" class="btn btn-info" :href="('http://localhost:9000/certificate/?cerid=').concat(certificate.id).concat('&cid=0')">Preview</a>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="certificate.id" @click="generateList(certificate.id)">Generate list</button>
				</div>	
				<div class="col">
					<button class="btn btn-secondary" :id="certificate.id" data-bs-toggle="modal" :data-bs-target="'#editCertificate'.concat(certificate.id)">Edit</button>
				</div>	
				<div class="col">
					<button class="btn btn-danger" :id="certificate.id" data-bs-toggle="modal" :data-bs-target="'#deleteCertificate'.concat(certificate.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addCertificateModal" tabindex="-1" aria-labelledby="addAnswerModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addCertificateHeader">Add certificate</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input type="text" name="" placeholder="Name of certificate" v-model="addCertificatePayload.name" class="form-control"><br>
						<input type="number" name="" placeholder="Cutoff mark" v-model="addCertificatePayload.cutoffMark" class="form-control"><br>
						<textarea v-model="addCertificatePayload.html" type="text" class="form-control" placeholder="HTML data"></textarea>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addAnswerBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="certificate in certificates" 
		class="modal fade" 
		:key="'modal_'.concat(certificate.id)"
		:id="'editCertificate'.concat(certificate.id)" 
		tabindex="-1" 
		aria-labelledby="editCertificate" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editCertificateHeader">Edit certificate</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="certificate.certificate_name" type="text" class="form-control" placeholder="Name">
						<br>
						<input v-model="certificate.cutoff_mark" type="number" class="form-control" placeholder="Cutoff mark">
						<br>
						<textarea class="form-control" v-model="certificate.html" placeholder="html"></textarea>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editCertificate(certificate.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="certificate in certificates" 
		class="modal fade" 
		:key="'modal_delete'.concat(certificate.id)"
		:id="'deleteCertificate'.concat(certificate.id)" 
		tabindex="-1" 
		aria-labelledby="deleteCertificateLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete certificate</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ certificate.certificate_name }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteCertificate(certificate.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>